.nav_container {
    width: 100%;
    height: 70px;
    background-color: #9ec8e5;
    margin-bottom: 50px;
}

.navbar_box {
    width: 100%;
    max-width: 80%;
    height: 70px;
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin: auto;
    padding: 0 20px;
}

.logo {
    width: 50px;
    height: 50px;
    border-radius: 15px;
}

.navbar_box a {
    text-decoration: none;
    color: #007BFF;
    font-size: 16px;
    display: inline-block;
    margin-left: 20px;
}

@media (max-width: 1200px) {
    .navbar_box {
        padding: 0 15px;
    }

    .navbar_box a {
        font-size: 14px;
    }
}

@media (max-width: 992px) {
    .navbar_box {
        justify-content: center;
    }

    .navbar_box a {
        font-size: 14px;
        margin-left: 15px;
    }
}

@media (max-width: 768px) {
    .navbar_box {
        flex-direction: column;
        align-items: center;
    }

    .logo {
        width: 40px;
        height: 40px;
    }

    .navbar_box a {
        font-size: 14px;
        margin-left: 0;
        margin-top: 10px;
    }
}

@media (max-width: 480px) {
    .navbar_box a {
        font-size: 12px;
        margin-left: 0;
        margin-top: 5px;
    }

    .logo {
        width: 30px;
        height: 30px;
    }
}
