.cartContainer {
    display: grid;
    max-width: 80%;
    margin: auto;
    grid-template-columns: 1fr 1fr 1fr 1fr 1fr; 
    grid-template-rows: 1fr 1fr; 
    gap: 20px 20px; 
    grid-template-areas: 
    ". . . . ."
    ". . . . .";
}

.btn_box {
    width: 300px;
    display: flex;
    margin: auto;
    margin-bottom: 50px;
    justify-content: space-around;
}

.active_btn, .inactive_btn {
    width: 100px;
    height: 50px;
    padding: 10px;
    border: none;
    border-radius: 20px;
    transition: .5s;
}

.active_btn {
    background-color: rgb(89, 89, 206);
    color: white;
}

@media (max-width: 1200px) {
    .cartContainer {
        grid-template-columns: 1fr 1fr 1fr 1fr;
    }
    .btn_box {
        width: 250px;
    }
}

@media (max-width: 992px) {
    .cartContainer {
        grid-template-columns: 1fr 1fr 1fr;
    }
    .btn_box {
        width: 200px;
    }
}

@media (max-width: 768px) {
    .cartContainer {
        grid-template-columns: 1fr 1fr;
    }
    .btn_box {
        width: 180px;
    }
}

@media (max-width: 480px) {
    .cartContainer {
        grid-template-columns: 1fr;
        grid-template-areas: 
        "."
        ".";
    }
    .btn_box {
        width: 150px;
    }
    .active_btn, .inactive_btn {
        width: 80px;
        height: 40px;
    }
}
